body {
  font-family: sans-serif;
}

button {
  margin: 50px auto 0;
  display: block;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

input, button {
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

input[type="submit"], button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(45, 45, 233, .73);
  border: none;
}

.container {
  text-align: center;
  width: 1200px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px 15px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 30px);
  margin: 0;
  overflow: hidden;
}

.photo-card img {
  width: 410px;
  height: 250px;
  object-fit: cover;
}

.info {
  justify-content: center;
  align-items: center;
  font-size: 12px;
  display: flex;
}

.info-item {
  margin-left: 10px;
  margin-right: 10px;
}

